<template>
    <div class="table-group">
        <div class="t-btn">
            <el-button @click="queryBeforeWeek">
            </el-button>
            <el-button @click="queryNextWeek"
                       type="info">
            </el-button>
        </div>
        <div class="t-btn" style="float: right;">
            <el-button @click="editUser = true">
            </el-button>
            <el-button @click="addUser = true"
                       type="info">
            </el-button>
        </div>
        <el-table
                :data="tableData"
                border
                tooltip-effect="dark"
                style="width: 100%">
            <el-table-column
                    header-align="center"
                    prop="schedule"
                    label="/">
            </el-table-column>
            <el-table-column
                    v-for="(item,key) in date"
                    header-align="center"
                    :key="key"
                    :prop="item"
                    :label="item">
            </el-table-column>
        </el-table>
        <el-dialog
                title=""
                v-model="editUser"
                size="tiny"
                @close="resetForm('addForm','editUser')">
            <div class="dialog-context">
                <el-form
                        label-position="right"
                        label-width="100px"
                        :model="addForm"
                        ref="addForm">
                    <el-form-item label=""
                                  prop="date">
                        <el-input v-model="addForm.date">
                        </el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="time">
                        <el-input v-model="addForm.time">
                        </el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="name">
                        <el-input v-model="addForm.name">
                        </el-input>
                    </el-form-item>
                </el-form>
            </div>
            <el-form slot="footer"
                     class="dialog-footer">
                <el-form-item>
                    <el-button type="primary"
                               @click="submitForm('addForm','editUser')">
                         
                    </el-button>
                    <el-button @click="editUser = false">
                         
                    </el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog
                title=""
                v-model="addUser"
                size="tiny"
                @close="resetForm('addForm','addUser')">
            <div class="dialog-context">
                <el-form
                        label-position="right"
                        label-width="120px"
                        :model="addForm"
                        ref="addForm">
                    <el-form-item label=""
                                  prop="date">
                        <el-input v-model="addForm.date">
                        </el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="time">
                        <el-input v-model="addForm.time">
                        </el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="name">
                        <el-input v-model="addForm.name">
                        </el-input>
                    </el-form-item>
                </el-form>
            </div>
            <el-form slot="footer"
                     class="dialog-footer">
                <el-form-item>
                    <el-button type="primary"
                               @click="submitForm('addForm','addUser')">
                         
                    </el-button>
                    <el-button @click="addUser = false">
                         
                    </el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
        data() {
            return {
                date: ['name1', 'name2', 'name3', 'name4', 'name5', 'name6', 'name7'],
                tableData: [
                    {
                        'schedule': '9:00~12:00',
                        'name1': '',
                        'name2': '',
                        'name3': '',
                        'name4': '',
                        'name5': '',
                        'name6': '',
                        'name7': '',

                    },
                    {
                        'date': '2017-04-20',
                        'schedule': '14:00~18:00',
                        'name1': 'ami',
                        'name2': 'ami',
                        'name3': 'ami',
                        'name4': 'ami',
                        'name5': 'ami',
                        'name6': 'ami',
                        'name7': 'ami',
                    }
                ],
                addUser: false,
                editUser: false,
                addForm: {
                    'date': "",
                    'time': "",
                    'name': ""
                }
            }
        },
        methods: {
            queryBeforeWeek(){
                alert("")
            },
            queryNextWeek(){
                alert("")
            },
            resetForm(formName, dialog){
                this[dialog] = false;
                this.$refs[formName].resetFields();
            },
            submitForm(formName, dialog){
                this[dialog] = false;
            }
        }
    }
</script>

<style lang="less" rel="stylesheet/less" scoped>

</style>


