<template>
    <div class="table-group">
        <div class="t-btn">
            <el-button @click="deleteAllSelection('userData')"></el-button>
        </div>
        <t-search></t-search>
        <el-table
                :data="userData"
                border
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange">
            <el-table-column
                    type="selection"
                    width="55">
            </el-table-column>
            <el-table-column
                    type="index"
                    width="50">
            </el-table-column>
            <el-table-column
                    :sortable=true
                    header-align="center"
                    prop="name"
                    label="">
            </el-table-column>
            <el-table-column
                    :sortable=true
                    header-align="center"
                    prop="orgname"
                    label="">
            </el-table-column>
            <el-table-column
                    :sortable=true
                    header-align="center"
                    prop="date"
                    label="">
            </el-table-column>
            <el-table-column
                    :sortable=true
                    header-align="center"
                    prop="standstarttime"
                    label="">
            </el-table-column>
            <el-table-column
                    :sortable=true
                    header-align="center"
                    prop="standendtime"
                    label="">
            </el-table-column>
            <el-table-column
                    :sortable=true
                    header-align="center"
                    prop="remark"
                    label="">
            </el-table-column>
            <el-table-column
                    header-align="center"
                    fixed="right"
                    label=""
                    width="100">
                <template scope="scope">
                    <el-button
                            @click.native.prevent="deleteRow(scope.$index, userData)"
                            type="text"
                            size="small">
                        
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <t-pagination></t-pagination>

        <el-dialog
                title=""
                v-model="addUser"
                size="tiny"
                @close="resetForm('addForm','addUser')">
            <div class="dialog-context"
                 style="position: relative; padding: 0 20px;">
                <el-form
                        label-position="right"
                        label-width="120px"
                        :model="addForm"
                        ref="addForm">
                    <el-form-item label=""
                                  prop="name"
                                  class="outer">
                        <el-input v-model="addForm.name">
                        </el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="pid"
                                  class="outer">
                        <el-input v-model="addForm.pid"></el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="orgid"
                                  class="outer">
                        <el-input v-model="addForm.orgid"></el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="outtime"
                                  class="outer">
                        <el-input
                                v-model="addForm.outtime">

                        </el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="precometime"
                                  class="outer">
                        <el-input
                                v-model="addForm.precometime">

                        </el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="outreason"
                                  class="outer">
                        <el-input
                                v-model="addForm.outreason">
                        </el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="remark"
                                  class="outer">
                        <el-input
                                v-model="addForm.remark">
                        </el-input>
                    </el-form-item>
                </el-form>
            </div>
            <el-form slot="footer">
                <el-form-item>
                    <el-button type="primary" @click="submitForm('addForm','addUser')"></el-button>
                    <el-button @click="addUser = false"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog
                :title="`${curRow.name}`"
                v-model="detailsDialog"
                size="tiny"
                @close="resetData('userData','personMoreData')">
            <div class="dialog-context"
                 style="position: relative; padding: 20px;">
                <div class="textBox">
                    <div>
                        <span></span>
                        <span>{{personMoreData[curIdx].pid}}</span>
                    </div>
                    <div>
                        <span></span>
                        <span>{{personMoreData[curIdx].pidtype}}</span>
                    </div>
                    <div>
                        <span></span>
                        <span>{{personMoreData[curIdx].birth}}</span>
                    </div>
                    <div>
                        <span></span>
                        <span>{{personMoreData[curIdx].military}}</span>
                    </div>
                    <div>
                        <span></span>
                        <span>{{personMoreData[curIdx].nation}}</span>
                    </div>
                    <div>
                        <span></span>
                        <span>{{personMoreData[curIdx].level}}</span>
                    </div>
                    <div>
                        <span></span>
                        <span>{{personMoreData[curIdx].duty}}</span>
                    </div>
                    <div>
                        <span></span>
                        <span>{{personMoreData[curIdx].property}}</span>
                    </div>
                    <div>
                        <span></span>
                        <span>{{personMoreData[curIdx].sex}}</span>
                    </div>
                    <div>
                        <span></span>
                        <span>{{personMoreData[curIdx].nativeplace}}</span>
                    </div>
                    <div>
                        <span></span>
                        <span>{{personMoreData[curIdx].retiredtime}}</span>
                    </div>
                    <div>
                        <span></span>
                        <span>{{personMoreData[curIdx].retiredreason}}</span>
                    </div>
                </div>
                <div>
                    <span></span>
                    <span>{{personMoreData[curIdx].serviceperformance}}</span>
                </div>
                <div>
                    <span></span>
                    <span>{{personMoreData[curIdx].department}}</span>
                </div>
                <div>
                    <span></span>
                    <span>{{personMoreData[curIdx].cpxondition}}</span>
                </div>
                <div>
                    <span></span>
                    <span>{{personMoreData[curIdx].remark}}</span>
                </div>
            </div>
            <div slot="footer"
                 class="dialog-footer">
            </div>
        </el-dialog>

    </div>
</template>
<script>
    import Vue from "vue"
    import TSearch from "../table/search.vue"
    import TPagination from "../table/pagination.vue"

    export default {
        components: {
            TSearch,
            TPagination
        },
        data() {
            return {
                curIdx: 0,
                curRow: '',
                changeMSG: false,
                addUser: false,
                detailsDialog: false,
                btnData: [
                    {
                        value: ""
                    },
                    {
                        value: ""
                    },
                ],
                addForm: {
                    'name': "",     //
                    'pid': "",         //
                    'orgid': "",        //
                    'outtime': "",           //
                    'precometime': "",
                    'outreason': "",
                    'remark': ""
                },
                userData: [
                    {
                        'id':'string',//id
                        'name':'string',//
                        'date':'string',//
                        'standstarttime':'string',//
                        'standendtime':'string',//
                        'orgname':'string',//
                        'remark':'string',//
                    },
                    {
                        'id':'string',//id
                        'name':'string',//
                        'date':'string',//
                        'standstarttime':'string',//
                        'standendtime':'string',//
                        'orgname':'string',//
                        'remark':'string',//
                    },
                    {
                        'id':'string',//id
                        'name':'string',//
                        'date':'string',//
                        'standstarttime':'string',//
                        'standendtime':'string',//
                        'orgname':'string',//
                        'remark':'string',//
                    },
                    {
                        'id':'string',//id
                        'name':'string',//
                        'date':'string',//
                        'standstarttime':'string',//
                        'standendtime':'string',//
                        'orgname':'string',//
                        'remark':'string',//
                    }

                ],
                personMoreData: [
                    {
                        'name': "varchar",                      //
                        'pid': "varchar",                      //
                        'pidtype': "int",                     //
                        'birth': "date",                     //
                        'military': "date",                  //
                        'nation': "int",                      //
                        'level': "int",                      //
                        'duty': "int",                       //
                        'property': "int",                   //
                        'sex': "int",                         //
                        'nativeplace': "varchar",            //
                        'department': "varchar",            //
                        'cpxondition': "int",              //
                        'picturepath': "varchar",          //
                        'retiredtime': "date",            //
                        'retiredreason': "text",          //
                        'serviceperformance': "text",       //
                    },
                    {
                        'name': "varchar",                      //
                        'pid': "varchar",                      //
                        'pidtype': "int",                     //
                        'birth': "date",                     //
                        'military': "date",                  //
                        'nation': "int",                      //
                        'level': "int",                      //
                        'duty': "int",                       //
                        'property': "int",                   //
                        'sex': "int",                         //
                        'nativeplace': "varchar",            //
                        'department': "varchar",            //
                        'cpxondition': "int",              //
                        'picturepath': "varchar",          //
                        'retiredtime': "date",            //
                        'retiredreason': "text",          //
                        'serviceperformance': "text",       //
                    },
                    {
                        'name': "varchar",                      //
                        'pid': "varchar",                      //
                        'pidtype': "int",                     //
                        'birth': "date",                     //
                        'military': "date",                  //
                        'nation': "int",                      //
                        'level': "int",                      //
                        'duty': "int",                       //
                        'property': "int",                   //
                        'sex': "int",                         //
                        'nativeplace': "varchar",            //
                        'department': "varchar",            //
                        'cpxondition': "int",              //
                        'picturepath': "varchar",          //
                        'retiredtime': "date",            //
                        'retiredreason': "text",          //
                        'serviceperformance': "text",       //
                    },
                    {
                        'name': "varchar",                      //
                        'pid': "varchar",                      //
                        'pidtype': "int",                     //
                        'birth': "date",                     //
                        'military': "date",                  //
                        'nation': "int",                      //
                        'level': "int",                      //
                        'duty': "int",                       //
                        'property': "int",                   //
                        'sex': "int",                         //
                        'nativeplace': "varchar",            //
                        'department': "varchar",            //
                        'cpxondition': "int",              //
                        'picturepath': "varchar",          //
                        'retiredtime': "date",            //
                        'retiredreason': "text",          //
                        'serviceperformance': "text",       //
                    }

                ],
                multipleSelection: []
            }
        },
        methods: {
            handleSelectionChange(val) {
                let Arr = [];
                val.forEach(function (el) {
                    Arr.push(el);
                })
                this.multipleSelection = Arr;
            },
            //            only use for userData ,this Array
            deleteAllSelection(data){
                let _self = this;
//              
                this[data] = this[data].filter(function (currentValue, currentIdx) {
                    let getItem = true;
                    _self.multipleSelection.forEach(function (val, idx) {
                        if (currentValue === val) {
                            getItem = false;
                        }
                    })
                    return getItem;
                });

            },
            detailsWatch(index, rows){
                this.detailsDialog = true;
                this.curIdx = index;
                this.curRow = rows[index];
                this.changeMSG = false;
            },
            deleteRow(index, rows) {
                rows.splice(index, 1);
            },
            resetForm(formName, dialog){
                this[dialog] = false;
                this.$refs[formName].resetFields();
            },
            submitForm(formName, dialog){
                this[dialog] = false;
            },
//            
            resetData(acceptData, giveData){
                let idx = this.curIdx;
                this[acceptData][idx].retiredtime = this[giveData][idx].retiredtime;
                this[acceptData][idx].retiredreason = this[giveData][idx].retiredreason;
                this[acceptData][idx].department = this[giveData][idx].department;
                this[acceptData][idx].serviceperformance = this[giveData][idx].serviceperformance;
                this[acceptData][idx].remark = this[giveData][idx].remark;
//                console.log(this.userData[idx])
            }
        },
    }
</script>

<style lang="less" rel="stylesheet/less">

</style>


